.table-container {
    text-align: center;
    margin: 50px auto;
    width: 80%
}

table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
}

td,
th {
    border: 1px solid #dddddd;
    text-align: center;
    padding: 8px;
}

tr {
    background-color: rgb(238, 238, 238);
    font-size: 20px;
}

tr:nth-child(even) {
    background-color: rgb(206, 206, 206);
}

td:nth-child(1),
th:nth-child(1),
td:nth-child(2),
th:nth-child(2) {
    left: 0;
    position: sticky;
    z-index: 9999;
    background-color: inherit;
    border: inherit;
}
